.art-grid {
  max-width: 100%;
  padding: 4px;
}

#background {
  object-fit: fill;
  min-width: 100vw;
  min-height: 100vh;
  z-index: -1;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.art-item {
  position: relative;
  overflow: hidden;
}

.art-item-cover {
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1;
  background-color: #fff;
  transition: opacity .2s;
  position: absolute;
  top: 0;
  left: 0;
}

.art-item-cover:hover {
  opacity: .75;
  cursor: pointer;
}

.art-item-cover p {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.art-grid-row {
  flex-direction: row;
  margin-bottom: 4px;
  display: flex;
}

.popup {
  width: 100vw;
  height: 100vh;
  z-index: 3;
  background-color: #000a;
  position: fixed;
  top: 0;
  left: 0;
}

.popup-content {
  width: 100vw;
  height: 100vh;
  object-fit: contain;
  object-position: center;
  position: fixed;
  top: 0;
  left: 0;
}

.popup-row {
  width: 100vw;
  height: 100vh;
  flex-flow: row;
  justify-content: space-between;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.popup-column {
  flex-flow: column;
  justify-content: space-between;
  display: flex;
}

.popup-item {
  color: #fff;
  z-index: 5;
  opacity: 1;
  background-color: #0007;
  padding: 8px;
  transition: opacity 1s;
}

.popup-item-hidden {
  opacity: 0;
}

.popup-item a {
  cursor: pointer;
}

.top-left {
  max-width: 50vw;
  white-space: pre-line;
  padding: 16px;
}

.top-left h2 {
  margin: 0;
  padding: 0;
  font-size: 32px;
}

.top-left p {
  margin: 8px 0 0;
  padding: 0;
}

.bottom-left {
  max-width: 64px;
}

.disabled {
  opacity: .33;
  cursor: default;
}

@media screen and (max-width: 1184px) {
  .art-grid-row {
    flex-direction: column;
    margin-bottom: 0;
  }

  .art-grid-row div {
    margin-bottom: 8px;
  }
}

